export default function loadLocale(locale) {
  return new Promise((resolve) => {
    switch (locale) {
    case 'en':
  
      case 'en-US': {
        const data = require('./en-US');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'en-GB': {
        const data = require('./en-GB');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'en-AU': {
        const data = require('./en-AU');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'fr':
  
      case 'fr-FR': {
        const data = require('./fr-FR');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'fr-CA': {
        const data = require('./fr-CA');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'de':
  
      case 'de-DE': {
        const data = require('./de-DE');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'it':
  
      case 'it-IT': {
        const data = require('./it-IT');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'es':
  
      case 'es-419': {
        const data = require('./es-419');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'es-ES': {
        const data = require('./es-ES');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'ja':
  
      case 'ja-JP': {
        const data = require('./ja-JP');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'pt':
  
      case 'pt-PT': {
        const data = require('./pt-PT');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'pt-BR': {
        const data = require('./pt-BR');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'zh':
  
      case 'zh-CN': {
        const data = require('./zh-CN');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'zh-TW': {
        const data = require('./zh-TW');
        return resolve(data.__esModule === true ? data.default : data);
      }
      case 'zh-HK': {
        const data = require('./zh-HK');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'nl':
  
      case 'nl-NL': {
        const data = require('./nl-NL');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'ko':
  
      case 'ko-KR': {
        const data = require('./ko-KR');
        return resolve(data.__esModule === true ? data.default : data);
      }
    case 'fi':
  
      case 'fi-FI': {
        const data = require('./fi-FI');
        return resolve(data.__esModule === true ? data.default : data);
      }
      default:
        return resolve(null);
    }
  });
}
